<template>
	<div>
		<!-- 头部 -->
		<div>
			<van-nav-bar title="文章详情" left-arrow @click-left="onClickLeft" />
		</div>
		<!-- 内容 -->
		<div class="content">
			<!-- 标题 -->
			<h3>{{title}}</h3>
			<div class="user">
				<div class="userinfo" style="display: flex;">
					<!-- 头像 -->
					<div class="img" style="width: 50px;height: 50px;">
						<img :src="avatar" alt="" srcset="" style="width: 100%;height: 100%;border-radius: 50%;">
					</div>
					<!-- 名称 时间 -->
					<div class="info">
						<p>{{author}}</p>
						<span>{{new Date(1666365377288).toLocaleDateString()}}</span>
					</div>
				</div>
				<!-- 右侧关注按钮 -->
				<div class="infobtn" style="margin-right: 20px;">
					<button class="guanzhu" v-show="ishow" @click="changeshow">+关注</button>
					<button class="guanzhu" v-show="!ishow" @click="changeshow">已关注</button>
				</div>
			</div>
		</div>
		<!-- 内容和图片 -->
		<div class="list" style="border-bottom: 1px solid #eee;">
			<p style="text-align: left;margin-left: 10px;margin-bottom: 10px;">{{content}}</p>
			<div class="img" style="width: 95%;margin: 0 auto;margin-bottom: 10px;">
				<img :src="imageSrc[0]" alt="内容图片" srcset="" style="width: 100%;height: 100%;">
			</div>
		</div>
		<!-- 评论列表 -->
		<div class="comments" style="margin-bottom: 60px;margin-top: 10px;">
			<div class="comment-header">
				<h3>精彩评论 {{comment}}</h3>
				<p>{{like}} 赞 | {{fav}} 收藏</p>
			</div>
			<div class="comment-text" style="height: 200px;">

			</div>
		</div>
		<!-- 底部评论区 -->
		<div class="footer">
			<div class="input">
				<van-icon name="edit" style="position: absolute;top: 2.13333vw;left: 2.13333vw;" />
				<input type="text" placeholder="写评论...">
			</div>
			<div class="icon">
				<van-icon name="chat-o" class="icons" />
				<van-icon name="star" class="icons" />
				<van-icon name="good-job" class="icons" />
				<van-icon name="share-o" class="icons" />
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import Toast from 'vant'
	export default {
		data() {
			return {
				name: 'details',
				content: '',
				title: '',
				imageSrc: '',
				time: '',
				author: '',
				like: '',
				comment: '',
				fav: '',
				avatar: '',
				ishow: true
			}
		},
		components: {},
		methods: {
			onClickLeft() {
				this.$router.go(-1)
			},
			changeshow() {
				this.ishow = !this.ishow
			}
		},
		created() {
			// 获取文章详情
			let userInfo = JSON.parse(localStorage.getItem('userInfo'));
			axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail', {
				article_id: this.$route.query.article_id,
				user_id: userInfo.uid
			}).then(res => {
				// console.log("文章详情获取" + res.data.msg);
				this.content = res.data.data.content
				this.title = res.data.data.title
				this.imageSrc = res.data.data.imageSrc
				this.time = res.data.data.time
				this.author = res.data.data.author
				this.like = res.data.data.like
				this.comment = res.data.data.comment
				this.fav = res.data.data.fav
				this.avatar = res.data.data.avatar
			}).catch(err => {
				Toast('未知错误')
			})
			// 获取评论列表
			axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list', {
				article_id: this.$route.query.article_id,
			}).then(res => {
				console.log(res.data);
			})
		}
	}
</script>

<style lang="less" scoped>
	/deep/ .van-nav-bar {
		background-color: #07c160;
	}

	/deep/.van-nav-bar .van-icon {
		color: #fff;
	}

	/deep/ .van-nav-bar__title {
		color: #fff;
	}

	.van-nav-bar {
		width: 100%;
		position: fixed;
		left: 0;
		top: 0;
	}

	.content {
		margin-bottom: 20px;
		margin-top: 60px;
	}

	.content>h3 {
		text-align: left;
		margin-left: 20px;
		margin-top: 20px;
		margin-bottom: 20px;
	}

	.info {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;
		margin-left: 10px;
	}

	.user {
		display: flex;
		margin-left: 20px;
		justify-content: space-between;
		align-items: center;
	}

	.comment-header {
		display: flex;
		justify-content: space-between;
		margin-left: 10px;
		margin-right: 10px;
	}

	.footer {
		display: flex;
		padding: 2.66667vw 4.26667vw;
		position: fixed;
		bottom: -1px;
		left: 0;
		background-color: #fff;
		border-top: 1px solid #f4f4f4;
	}

	.footer .input {
		position: relative;
		flex: 4;
		display: flex;
	}

	.input>input {
		width: 100%;
		background-color: #f3f3f3;
		padding: 2.13333vw 0 2.13333vw 9.33333vw;
		border-radius: 4vw;
		outline: none;
		border: none;
	}

	.footer .icon {
		// flex: 6;
		text-align: center;
		height: 9.33333vw;
		line-height: 11.33333vw;
	}

	.footer .icon .icons {
		margin: 0 4.26667vw 2.66667vw 2.66667vw;
		font-size: 6.4vw;
	}

	.guanzhu {
		border: 1px solid #ccc;
		height: 8vw;
		line-height: 8vw;
		width: 13.33333vw;
		text-align: center;
		font-size: 3.73333vw;
		margin-top: 1px;
		background-color: #fff;
		color: #000;
	}
</style>
